import React from 'react';
import { Link } from 'react-router-dom';
import resume from '../../images/Resume.svg';
import './Registration.css';
import { useForm } from 'react-hook-form';
import api from '../../utils/Api';
import { isValidPhoneNumber } from 'react-phone-number-input';
import { useDispatch } from 'react-redux';
import { setUser } from '../store/slices/userSlice';

const Registration = () => {
  const {
    register,
    formState: { errors, isValid },
    handleSubmit,
    reset,
    watch,
  } = useForm({
    mode: 'onChange',
  });
  const dispatch = useDispatch();
  const [isError, setIsError] = React.useState('');
  const mailField = watch('email');
  const onSubmit = (data) => {
    api
      .register(data)
      .then((res) => {
        dispatch(
          setUser({
            email: res.email,
            name: res.name,
            surname: res.surname,
            token: res.token,
            id: res.id,
            phone: res.phone,
          })
        );
        reset();
      })
      .catch((er) => {
        api.checkEmail(mailField).then((res) => {
          console.log(res);
          res ? setIsError('   ') : setIsError('');
        });
      });
  };

  return (
    <section className='registration'>
      <div className='registration__container'>
        <h1 className='registration__title'></h1>
        <form className='registration__form' onSubmit={handleSubmit(onSubmit)}>
          <span className='error-message'>{errors?.name?.message}</span>
          <input
            type='text'
            className={`registration__input ${
              errors?.name && 'registration__input-error'
            }`}
            placeholder=''
            {...register('name', {
              required: '    ',
              minLength: {
                value: 2,
                message: '   2 ',
              },
              maxLength: {
                value: 32,
                message: '   32 ',
              },
            })}
          />
          <span className='error-message'>{errors?.surname?.message}</span>
          <input
            type='text'
            className={`registration__input ${
              errors?.surname && 'registration__input-error'
            }`}
            placeholder=''
            {...register('surname', {
              required: '    ',
              minLength: {
                value: 2,
                message: '   2 ',
              },
              maxLength: {
                value: 32,
                message: '   32 ',
              },
            })}
          />
          <span className='error-message'>{errors?.email?.message}</span>
          <input
            className={`registration__input && ${
              errors?.email && 'registration__input-error'
            }`}
            placeholder=''
            autoComplete='none'
            {...register('email', {
              required: '    ',

              pattern: {
                value: /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/,
                message: '    ',
              },
              minLength: {
                value: 7,
                message: '    7 ',
              },
            })}
          />
          <span className='error-message'>{errors?.phone?.message}</span>
          <input
            className={`registration__input ${
              errors?.phone && 'registration__input-error'
            }`}
            placeholder=''
            type='tel'
            {...register('phone', {
              validate: {
                phone: (value) => {
                  return value
                    ? isValidPhoneNumber(value) || '   +7'
                    : true;
                },
              },
            })}
          />
          <span className='error-message'>{errors?.password?.message}</span>
          <input
            className={`registration__input ${
              errors?.password && 'registration__input-error'
            }`}
            placeholder=''
            type='password'
            {...register('password', {
              required: '    ',
              minLength: {
                value: 3,
                message: '   3 ',
              },
              maxLength: {
                value: 32,
                message: '   32 ',
              },
              validate: {
                numb: (value) =>
                  /\d/g.test(value) ||
                  '       ',
                symb: (value) =>
                  /[a-zA-Z]/g.test(value) ||
                  '       ',
                special: (value) =>
                  /[!@#$%^&*()=\-]/g.test(value) ||
                  '         (!@#$%^&*()=-)',
              },
            })}
          />
          <span className='error-message registration-error'>{isError}</span>
          <div className='registration__buttons'>
            <input
              type='submit'
              disabled={!isValid}
              className={`registration__submit-button ${
                !isValid && 'registration__submit-button-inactive'
              }`}
              value=''
            />
            <Link to='/login' className='registration__button'>
              
            </Link>
          </div>
        </form>
      </div>
      <img src={resume} alt='' className='registration__image' />
    </section>
  );
};

export default Registration;
