import React from "react";
import starImage from "../../../images/star.svg";
import benefitsImage from "../../../images/onlineClass.svg";
import "./Benefits.css";
import { Link } from "react-router-dom";
function Benefits(props) {
  return (
    <section className="benefits">
      <p className="benefits__heading">
                
         .
      </p>
      <div className="benefits__content">
        <div className="benefits__container">
          <h2 className="benefits__title"></h2>
          <ul className="benefits__list">
            <li className="benefits__list-item">
              <img
                src={starImage}
                alt=""
                className="benefits__list-icon"
              />
              <p className="benefits__list-paragraph">
                       
              </p>
            </li>
            <li className="benefits__list-item">
              <img
                src={starImage}
                alt=""
                className="benefits__list-icon"
              />
              <p className="benefits__list-paragraph">
                      Telegram
              </p>
            </li>
            <li className="benefits__list-item">
              <img
                src={starImage}
                alt=""
                className="benefits__list-icon"
              />
              <p className="benefits__list-paragraph">
                  ,    
                 
              </p>
            </li>
          </ul>
          <Link to="/courses" className="benefits__button">
             
          </Link>
        </div>
        <img
          src={benefitsImage}
          alt=" "
          className="benefits__image"
        />
      </div>
    </section>
  );
}
export default Benefits;
